namespace $ { $mol_style_attach( "mrtest/mrtest.view.css",
 "[mrtest_output_page],\n[mrtest_source_page] {\n\tflex-grow: 0;\n\tflex-basis: 800px;\n\tmargin: 0 auto;\n}\n\n[mrtest_menu],\n[mrtest_profile_page],\n[mrtest_access_page] {\n\tflex-grow: 0;\n\tflex-basis: 320px;\n}\n\n[mrtest_validator] {\n\tmargin-bottom: var(--mol_gap_block);\n}\n\n[mrtest_validator_title_row] {\n\tpadding: 3px 0;\n\tgap: 3px;\n}\n\n[mrtest_test_item] {\n\tpadding: 0;\n\tjustify-content: space-between;\n}\n\n[mrtest_test_item_link] {\n\tflex-grow: 1;\n}\n\n[mrtest_test_item_options] {\n\topacity: .5;\n}\n\n[mrtest_test_item_options]:hover {\n\topacity: 1;\n}\n\n[mrtest_create_test_button],\n[mrtest_search_input] {\n\tmargin-bottom: var(--mol_gap_block);\n}\n\n:root {\n\t--mol_theme_hue: 195deg;\n\t--mol_theme_hue_spread: 90deg;\n}\n\n:root, [mol_theme=\"$mol_theme_dark\"] {\n\t--mol_theme_luma: -1;\n\t--mol_theme_image: invert(1) hue-rotate(180deg);\n\n\t--mol_theme_back: hsl( var(--mol_theme_hue), 5%, 10% );\n\t--mol_theme_card: hsl( var(--mol_theme_hue), 5%, 20%, .25 );\n\t--mol_theme_field: hsl( var(--mol_theme_hue), 5%, 8%, .25 );\n\t--mol_theme_hover: hsl( var(--mol_theme_hue), 0%, 35%, .1 );\n\n\t--mol_theme_text: hsl( var(--mol_theme_hue), 5%, 80% );\n\t--mol_theme_shade: hsl( var(--mol_theme_hue), 5%, 60%, 1 );\n\t--mol_theme_line: hsl( var(--mol_theme_hue), 5%, 50%, .25 );\n\t--mol_theme_focus: hsl( calc( var(--mol_theme_hue) + 180deg ), 50%, 65% );\n\n\t--mol_theme_control: hsl( var(--mol_theme_hue), 10%, 65% );\n\t--mol_theme_current: hsl( calc( var(--mol_theme_hue) - var(--mol_theme_hue_spread) ), 10%, 65% );\n\t--mol_theme_special: hsl( calc( var(--mol_theme_hue) + var(--mol_theme_hue_spread) ), 10%, 65% );\n}\n\n:root, [mol_theme=\"$mol_theme_light\"] {\n\t--mol_theme_luma: 1;\n\t--mol_theme_image: none;\n\n\t--mol_theme_back: hsl( var(--mol_theme_hue), 5%, 92% );\n\t--mol_theme_card: hsl( var(--mol_theme_hue), 5%, 100%, .5 );\n\t--mol_theme_field: hsl( var(--mol_theme_hue), 5%, 100%, .75 );\n\t--mol_theme_hover: hsl( var(--mol_theme_hue), 5%, 50%, .1 );\n\n\t--mol_theme_text: hsl( var(--mol_theme_hue), 5%, 0% );\n\t--mol_theme_shade: hsl( var(--mol_theme_hue), 5%, 40%, 1 );\n\t--mol_theme_line: hsl( var(--mol_theme_hue), 5%, 50%, .5 );\n\t--mol_theme_focus: hsl( calc( var(--mol_theme_hue) + 180deg ), 50%, 40% );\n\n\t--mol_theme_control: hsl( var(--mol_theme_hue), 10%, 10% );\n\t--mol_theme_current: hsl( calc( var(--mol_theme_hue) - var(--mol_theme_hue_spread) ), 10%, 30% );\n\t--mol_theme_special: hsl( calc( var(--mol_theme_hue) + var(--mol_theme_hue_spread) ), 10%, 30% );\n\n}\n\n[mol_book2] > *:not(:first-of-type):before,\n[mol_book2] > *:not(:last-of-type)::after {\n\tbackground: none;\n}\n\n\n[mol_view_error] {\n\tcolor: black !important;\n}\n"
) }